<script>
import LeadDataService from "../../../services/DataService";
import $ from "jquery";
export default {
  props: {
    userid: {
      type: Number,
      required: true,
    },
    companyid: {
      type: Number,
      required: true,
    },
    id: {
      type: String,
      required: true,
    },
    modulename: {
      type: String,
      required: true,
    },
    submodulecode: {
      type: String,
      required: true,
    },
    containername: {
      type: String,
      required: true,
    },
    maxsize: {
      type: String,
    },
    disallowedfiletypes: {
      type: String,
    },
    imagesizedata: {
      type: String,
    },
  },
  data: function () {
    return {
      isFileValidation:false,
      fileData: [],
      DisplayName: "",
      FileName: "",
      btnDisable: false,
      isLoading: false
    };
  },
  // created: function () {
  //   var vm=this;  
  //   setTimeout(function(){vm.ShowPopOver()}, 500);
  // },
  methods: {
    checkForDisplayName: function (e) {
      if (!this.DisplayName) {       
        e.preventDefault();
      }
    },
    CheckAttach: function (DisplayName) {     
      if (DisplayName != "") {
        this.btnDisable = true;
        this.fileData.fileName = DisplayName;       
      } else {
        this.btnDisable = false;       
      }    
    },
    GetBase64String: function (evt) {   
      debugger;
      var vm = this;
      vm.fileData = [];       
      var f = evt.target.files[0]; 
      var reader = new FileReader();    
      reader.onload = (function (theFile) {
        // vm.$refs.attachment.setFieldTouched('FileSrc', false);
        return function (e) {
          var binaryData = e.target.result;
          var base64StringEncoded = window.btoa(binaryData);
          var fileName = f.name;
          var n = fileName.lastIndexOf(".");
          var extensionName = fileName.substring(n + 1, fileName.length);
          var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
          if (vm.DisplayName == "") {
            fileName = f.name;
            vm.FileName = f.name;
          } else {
            fileName = vm.DisplayName;
            vm.FileName = f.name;
          }
          var fileObj = {
            attachement_mime: attachemnetMime,
            fileName: fileName,
            userId: vm.userid,
            companyId: vm.companyid,
            id: vm.id,
            moduleName: vm.modulename,
            subModuleCode: vm.submodulecode,
            containerName: vm.containername,
            base64String: base64StringEncoded,
          };
          vm.fileData.push(fileObj);    
          vm.isFileValidation=false;      
        };
      })(f);      
      reader.readAsBinaryString(f);
    },
    byteToSize: function (bytes) {
     if (bytes == 0) return "0 Byte";
    return bytes / Math.pow(1024, 2);
    },
    onClickFile(){
    debugger;
    var inputElement = document.getElementById('inputAttach');
        var files = inputElement.files;
        if(files.length==0)
          this.isFileValidation=true;
          else
          this.isFileValidation=false;
    },
    SaveAttachment: function () {
      debugger;
      var vm = this;
      var uploadedsize = 0;
      var extensionName = "";
      this.onClickFile();
    if ($("#inputAttach").val() != "") {
        uploadedsize = vm.byteToSize(
          JSON.parse(vm.fileData[0].attachement_mime).Size
        );
        extensionName = JSON.parse(vm.fileData[0].attachement_mime).Extension;
      }
      var maxsize = parseFloat(vm.maxsize) || 0;
      if (uploadedsize > maxsize && maxsize != 0) {
        vm.ShowAlert(("Size exceeds than " + maxsize + " MB. Please upload file with valid size."),"warning", true, vm.$t("Alert"));
        return false;
      }
      var disallowedfileExt =
        vm.disallowedfiletypes != undefined? vm.disallowedfiletypes.split(",") : ""; 
      var uploadfileext = "." + extensionName;
      if (disallowedfileExt.includes(uploadfileext)) {
        vm.ShowAlert(vm.$t("InvalidFileUploadMsg"),"warning", true, vm.$t("Alert"));
        return false;
      }
      
      this.$refs.attachment.validate().then((result) => {  
        // if(result.errors.hasOwnProperty('FileSrc')){
        //         delete result.errors.FileSrc;
        //             result.results['FileSrc'].valid=true;
        //             result.results['FileSrc'].errors=[];
        //             result.valid= Object.keys(result.errors).length === 0?true:result.valid;
        //     }
        console.log(vm.$refs.attachment)
        if (result) {         
          vm.isLoading = true; 
          var objectPostString = JSON.stringify(vm.fileData);
          if (disallowedfileExt.includes(uploadfileext)) {
            vm.ShowAlert(vm.$t("InvalidFileUploadMsg"));             
          } else {
            LeadDataService.UploadFile(objectPostString).then(function (response) {  
              vm.isLoading = false;
              if (response.data[0].Status == "Success") { 
                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Attachment')]), "Success", true, vm.$t("Alert"));
                vm.$emit("close");
                vm.$parent.loadData("attachments");              
              } else if (response.data[0].Status == "Exist") {
                vm.ShowAlert(vm.$t("FileNameAlreadyExist",[vm.$t('Attachment')]),"warning", true, vm.$t("Alert"));
              } else {
                vm.ShowAlert(vm.$t("ValidFileType",[vm.$t('Attachment')]),"failure", true, vm.$t("Alert"));
              }               
            });
          }
        }
      });
    },
  },
};
</script>
<template>
   
  <transition name="modal">
   
    <div class="modal d-block my-popups dialog">
      <loader :is-visible="isLoading"></loader>
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <slot name="headerForModal">
              <h5 class="modal-title">{{ $t('Attachments') }}</h5>
            </slot>
            <button type="button" v-on:click="$emit('close')" href="javascript:;" class="close">
              <em class="fa fa-times"></em>
            </button>
          </div>
          <div class="modal-body">
            <div class="row form-head" style="display:none;">
              <div class="col-12">
                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-right p-0">
                  <div class="userguide-box userguide_curve">
                    <a data-toggle="collapse" href="#user-guidepriority" aria-expanded="false"
                      aria-controls="search-content" class="userguide-btn collapsed">
                      <em class="fa"></em>
                      <span>{{ $t('GuideForUser') }}</span>
                    </a>
                    <div class="clearfix"></div>
                    <div id="user-guidepriority" class="user-guide collapse">
                      {{ $t('GuideForUser') }}
                      <br />
                      <br />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <Form ref="attachment" >
              <div class="form-content">
                <div class="col-md-12 p-0">
                  <div class="row">
                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6">
                      <div class="form-group">
                        <label>{{ $t('DisplayName') }}:</label>
                        <span class="text-danger">*</span>
                        <Field name="Name" :rules="{ 'required': true }" v-slot="{ errors}">
                          <input  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" type="text" maxlength="50" v-model="DisplayName"
                            id="DisplayName" name="DisplayName" v-on:keyup="CheckAttach(DisplayName)" class="form-control" />
                          <span  v-if="errors[0]" name="DisplayName" class="invalid-feedback">{{ errors[0] }}</span>
                        </Field>
                      </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6" id="attach">
                      <div class="form-group">
                        <label>{{ $t('Attachment') }}:</label>
                        <span class="text-danger">*</span>
                        <a href="javascript:;" v-if="imagesizedata != undefined && imagesizedata.length > 0"
                          data-toggle="popoveruserguide" v-popover:tooltip="imagesizedata">
                          <em class="fa fa-question-circle-o text-popover" style="font-size: 14p  x;"></em>
                        </a>
                        <div class="custom-file">
                          <Field name="File" ref="provider" :rules="{'required': true }"  v-slot="{errors}">
                            <input  v-bind:class="{'form-control': true, 'is-invalid':errors[0]|| isFileValidation==true}" type="file" name="FileSrc"
                             id="inputAttach" v-on:change="GetBase64String" @click="onClickFile($event)" />
                             <span v-if="isFileValidation==true" class="invalid-feedback d-block">The Attachment is required</span>
                            <span v-else-if="errors[0]" class="invalid-feedback d-block">{{ errors[0] }}</span>
                          </Field>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Form>
          </div>
          <div class="modal-footer text-right">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
              <div class="form-group">
                <div class="search-btm-btn">
                  <a href="javascript:;" :title="$t('Save')" class="btn btn-success  save-btn mr-2"
                    v-on:click="SaveAttachment()">
                    <em class="fa fa-save pr-2"></em>
                    {{ $t('Save') }}
                  </a>
                  <a href="javascript:;" :title="$t('Cancel')" v-on:click="$emit('close')"
                    class="btn btn-danger delete-btn">
                    <em class="fa fa-times pr-1"></em>
                    {{ $t('Cancel') }}
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>