<script>
   import ClassicEditor from '@ckeditor/ckeditor5-build-classic'; 
export default {
    props: {
        callFrom: null,
        userid: {
            type: Number,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        companylogo:null        
    },
    data() {
        return {
            emailSubject: '',
            emailTo: '',
            emailCc: '',
            emailImportance: 'normal',
            emailBcc: '',
            emailContent: '',
            isView: false,
            modal: null,
            editor: ClassicEditor 
        }
    },
    created: function () {
        var vm =this;
        vm.modal = vm.modalItem;
        if (vm.modal != null) {
            vm.FetchEmailViewData(vm.modal.ID);
            vm.isView = true;
        } else {
            setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
        }
    },

    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
      
        SendEmail: function (values,meta) {
            debugger
            var resultBody = this.callFrom == 'ViewPurchaseOrderDetails' ? $("#purchaseOrderSendMail").html() : 'ViewQuotationDetails'? $("#quotationSendMail").html() : $("#invoiceSendMail").html();
        //    this.$refs.form.setFieldTouched('emailSubject', true); 
        //    this.$refs.form.setFieldTouched('emailTo', true); 
           this.$refs.form.validate().then((result) => {
                if (result) {
                    var vm = this;
                    var resultSendMeCopy = false;
                    var isIncludePdf = false;
                    if ($("#SendMeCopy").prop("checked")) {
                        resultSendMeCopy = true;
                    }
                    else {
                        resultSendMeCopy = false;
                    }
                    if ($("#isIncludePdf").prop("checked")) {
                        isIncludePdf = true;
                    }
                    else {
                        isIncludePdf = false;
                    }
                    var SendBill = {
                        from: vm.email,
                        to: vm.emailTo,
                        Subject: vm.emailSubject,
                        body: resultBody,
                        configModuleName: 'Email',
                        companyId: vm.companyid,
                        userId:vm.userid,
                        moduleName: vm.$route.params.moduleName,
                        subModuleCode: vm.$route.params.subModuleCode,
                     
                        emailCc: vm.emailCc,
                        sendMeCopy: resultSendMeCopy,
                        refId: vm.$route.params.id,
                        companyLogo: vm.companylogo,
                        mailStatus: 1,
                        quoteCompanyLogo: 'quotationLogo',
                        isSaveLog: '',
                        emailContent: vm.emailContent,
                        includePdf: isIncludePdf,
                    };
                    $.ajax({
                        cache: false,
                        type: "POST",                       
                        contentType: "application/json",
                        dataType: "json",
                        data: JSON.stringify(SendBill),
                       
                        success: function (response) {
                            if (response.status != null && response.status != "" && response.status == "success") {
                                vm.ShowAlert(vm.$t('EmailSentSuccessMessage',[vm.$t(SendBill.to)]), "success", true, vm.$t("Alert"));
                                vm.$emit('close');
                                vm.loadData('emailLog');
                                setTimeout(function () {
                                    box.modal('hide');
                                }, 1500);
                                 
                            }
                            else {
                                $('#Fields_mandatory').show();
                            }
                        }                        
                    });
                }
                else {
                    $('#Fields_mandatory').show();
                }
            });
        },
        FetchEmailViewData: function (id) {
            var vm = this;
            LeadDataService.EmailListing(url).then(function (response) {
                 
                if (response.data != null) {
                    var data = response.data.data;
                    vm.emailSubject = data[0].EMAIL_SUBJECT;
                    vm.emailTo = data[0].EMAIL_TO;
                    vm.emailCc = data[0].EMAIL_CC;
                    vm.emailImportance = data[0].EMAIL_PRIORITY;
                    vm.emailBcc = data[0].EMAIL_BCC;
                    vm.emailContent = data[0].EMAIL_CONTENT;
                }
            });
        }
    }
}
</script>
<template>
    <transition name="modal">
        <div class="modal d-block my-popups dialog">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">{{$t('SendEmail')}}</h5>
                        <button v-on:click="$emit('close')" type="button" title="" class="close"
                            data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <Form ref="form">
                            <div class="black-header " style="display:none">
                                <div class="row form-head">
                                    <div class="col-sm-12 col-md-6 col-lg-8 col-xl-8 float-left"><span
                                            class="form-title">{{$t('SendEmail')}}</span></div>
                                    <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                        <div class="userguide-box">
                                            <a data-toggle="collapse" href="#user-guidepriority" aria-expanded="false"
                                                aria-controls="search-content" class="userguide-btn collapsed">
                                                <em class="fa"></em><span>{{$t('User_Guide')}}</span></a>
                                            <div class="clearfix"></div>
                                            <div id="user-guidepriority" class="user-guide collapse">
                                                {{$t('FillFormtosendemail')}}<br><br>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Subject')}}:<span class="text-danger">*</span></label>
                                                <Field name="emailSubject" :rules="{'required':true}" label="Email Subject"
                                                    v-slot="{errors,touched,invalid}">
                                                    <input type="text" v-model="emailSubject"
                                                        name="emailSubject" v-bind:disabled="isView"
                                                        v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" />
                                                    <span   name="emailSubject"
                                                        class="invalid-feedback">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left">
                                            <div class="form-group">
                                                <label>{{$t('To')}}:<span class="text-danger">*</span></label>
                                                <Field name="emailTo" :rules="{'required':true,'email':true}" label="Email To"
                                                    v-slot="{errors,touched,invalid}">
                                                    <input  type="text" v-model="emailTo" name="emailTo"
                                                        v-bind:disabled="isView"
                                                        v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" />
                                                    <span   name="emailTo"
                                                        class="invalid-feedback">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Cc')}}:</label>
                                                <Field name="Cc" :rules="{'required':false,'email':true}" label="Email Cc"
                                                    v-slot="{errors,touched,invalid}">
                                                    <input  type="text" v-model="emailCc" name="Cc"
                                                        v-bind:disabled="isView" v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" />
                                                    <span   name="Cc"
                                                        class="invalid-feedback">{{ errors[0] }}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left"></div>
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left">
                                            <div class="form-group">
                                                <label class="form-check-label mt-1">Send me a copy:</label>
                                                <div class="form-check p-0">
                                                    <input id="SendMeCopy" data-change-id="no"
                                                        v-bind:disabled="isView" class="ml-3" value="1"
                                                        type="checkbox">
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 p-2 float-left">
                                            <div class="form-group">
                                                <label class="form-check-label mt-1">Send Purchase Order as PDF:</label>
                                                <div class="form-check p-0">
                                                    <input id="isIncludePdf" data-change-id="no"
                                                        v-bind:disabled="isView" class="ml-3" value="1"
                                                        type="checkbox">
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-2 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Description')}}:</label>
                                                <Field name="emailContent" :rules="{'required':false}"
                                                    label="emailContent" v-slot="{errors,touched,invalid}">

                                                    <ckeditor  :editor="editor" @ready="onReady"
                                                        v-model="emailContent" :config="editorConfig"
                                                        style="height: '500px' "></ckeditor>
                                                    <span   name="emailContent"
                                                        class="invalid-feedback d-block">{{errors[0]}}</span>
                                                </Field>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" :title="$t('Send')" class="btn btn-success mr-1"
                                                v-on:click="SendEmail(values,meta)"><em class="fa fa-send pr-2"></em>
                                                {{$t('Send')}}</a>
                                            <a href="javascript:;" :title="$t('Cancel')" v-on:click="$emit('close')"
                                                class="btn btn-danger"><em class="fa fa-times"></em>{{$t('Cancel')}}</a>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2"></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>