<template>
     <div class="listing mt-2">
        <loader :is-visible="isLoading"></loader>
            <div v-if="NoRecordForTab" class="text-center text-danger p-4 border">{{$t('NoRecordfound')}}</div>
            <div class="row custom-box-width">
                <div class="col-xl-2 col-lg-4 col-md-6" v-for="(item, index) in AttachmentData" :key="index">
                    <div class="box border pt-4 text-center">
                        <div class="verticle-action">
                                <em class="fa fa-ellipsis-v action_icon"  @click="openActionIcon($event)" aria-hidden="true"></em>
                                <span class="action-list-box" style="width: 240px;display:none;">
                                    <span class="list-actions" id="action-list" style="padding-left:0">
                                        <a data-toggle="modal" href="javascript:void(0)" class="actions-onclick edit-list" v-on:click="downloadAttachment(item)"><em class="fa fa-download" aria-hidden="true"></em><br>Download</a>
                                        <a v-bind:href="item.attachement_path" v-if="returntFileIconClass(item) == 'fa-file-image-o'" target="_blank" class="actions-onclick view-list"><em class="fa fa-eye"></em><br>View</a>
                                        <a v-on:click="deleteAttachment(item)"  class="actions-onclick view-list"><em class="fa fa-trash"></em><br>Delete</a>
                                        <em class="fa fa-times close close-action" @click="closeActionIcon($event)" aria-hidden="true"></em>
                                    </span>                                   
                                </span>
                            </div>
                       <div v-bind:class="{'img-ico text-center p-5 file-image doc-icon':(item.thumb_nail_path == null || item.thumb_nail_path == ''), 'img-ico fix145 text-center pb-4 file-image doc-icon pt-3':(item.thumb_nail_path != null && item.thumb_nail_path != '')}">
                        <img v-bind:src="item.thumb_nail_path" alt="" loading="'lazy'" v-if="item.thumb_nail_path != null && item.thumb_nail_path != ''"/>
                        <em v-bind:class="'fa ' + returntFileIconClass(item)" v-if="item.thumb_nail_path == null || item.thumb_nail_path == ''"></em>
                        </div>
                        <hr class="mb-0">
                        <div class="clearfix"></div>
                        <div class="row mx-0 my-2">
                            <div class="col-md-12 text-left text-truncate font-14 mb-2 font-weight-bold" v-bind:title="item.attachement_name+'.'+ (JSON.parse(item.attachement_mime)).Extension">{{item.attachement_name+'.'+ (JSON.parse(item.attachement_mime)).Extension}}</div>
                            <div class="col-md-5 font-14 align-self-center text-left pr-0"><span>{{bytesToSize((JSON.parse(item.attachement_mime)).Size)}}</span></div>
                            <div class="col-md-7 text-right pl-0 text-truncate"><small>{{item.created_at| formatDateTime}}</small></div>
                            
                        </div>
                    </div>
                </div>
                
            </div>
     </div>
</template>
<script>
import $ from "jquery";
import LeadDataService from "../../../services/DataService";
export default {
    props: {
        attachmentdata: null,
         
        sourceid: {
            type: String
        },        
    },
    data: function () {
      
        return {
            isLoading: false,
            AttachmentData: [],
            NoRecordForTab: true,
            LeadStatus: this.$parent.LeadStatus,
            showAttachmentModal: false,
            imageUrl:'',
        }
    },
    watch: {
        attachmentdata: function (value) {
            var LoggedUser = $("#hdnLoggedInUserId").val();
            $.each(value, function (indx, item) {
                if (item.created_by == LoggedUser) {
                    item.IsDelete = true;
                }
                else
                    item.IsDelete = false;
            });
            this.AttachmentData = value;
            this.NoRecordForTab = (this.AttachmentData != null && this.AttachmentData.length > 0) ? false : true;
            setTimeout(function () {
                $(".action_icon").click(function () {
                    $(this).parent().find(".action-list-box").show(500);
                });
                $(".close-action").click(function () {
                    $(this).parent().parent(".action-list-box").hide(500);
                });
            }, 1000);
        }
    },
    methods: {
        openActionIcon(e){
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        closeActionIcon(e){
            e.target.nextSibling.style.display = 'none';
        },
        StrTruncate: function (str) {
            return StrTruncate(str, 25);
        },
        returntFileIconClass: function (item) {
            var imgExt = ["JPG", "JPEG", "BMP", "GIF", "PNG"];
            var attchMime = JSON.parse(item.attachement_mime);
            var ext = attchMime.Extension.toUpperCase();
            if (ext == "COMPRESSEDFILES" || ext == "ZIP" || ext == "RAR") {
                return "fa-file-archive-o";
            } else if (ext == "XLS" || ext == "XLSX" || ext == "DATAFILES") {
                return "fa-file-excel-o";
            } else if (ext == "PPT") {
                return "fa-file-powerpoint-o";
            } else if (ext == "PDF") {
                return "fa-file-pdf-o";
            } else if (ext == "DOC" || ext == "DOCX") {
                return "fa-file-word-o";
            } else if (ext == "TXT") {
                return "fa-file-text-o";
            } else if (imgExt.indexOf(ext) > -1) {
                return "fa-file-image-o";
            } else {
                return "fa-file-o";
            }
         
        },
        bytesToSize: function (bytes) {
            var sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];
            if (bytes == 0) return '0 Byte';
            var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)));
            return Math.round(bytes / Math.pow(1024, i), 2) + ' ' + sizes[i];
        },
        downloadAttachment: function (item) {
            this.isLoading=true;
            var vm=this;
            var param=`fileId=${item.attachement_id}&filePath=${item.blob_path}&fileType=${((JSON.parse(item.attachement_mime)).ContentType)}&fileName=${(item.attachement_name + "." + (JSON.parse(item.attachement_mime)).Extension)}&downloadType=single`;
            LeadDataService.CRMDownloadFile(param).then(function (response) {
                vm.isLoading=false;
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', (item.attachement_name + "." + (JSON.parse(item.attachement_mime)).Extension));
                document.body.appendChild(fileLink);
                fileLink.click();
                    });           
        },
        deleteAttachment: function (item) {
           
            var vm = this;
                vm.confirmR(this.$t("ConfirmDelete"), true, (this.$t("Delete") + "  " + this.$t("Attachment")), function () {
                    vm.isLoading=true;
                    var attach_id = item.attachement_id;
                    var src_id = parseInt(vm.sourceid);
                    var Atturl = "id=" + attach_id + "&sourceid=" + src_id;
                    LeadDataService.DeleteAttachment(Atturl).then(function (response) {
                        vm.isLoading=false;
                        if (response.data == 'SUCCESS') {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Attachment')]), "success", true, vm.$t("Alert"));  
                            vm.$parent.loadData("attachments");
                        }
                    });
                });
            
         }
    }
}
</script>