<script>
import LeadDataService from "../../../services/DataService";
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
export default {
    props: {
       
        userid: {
            type: Number,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        id: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        email: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        containername: {
            type: String,
            required: true
        },
        defaultemailid: {
            type: String,
            required: false
        },
      
        EmailInfo:null,
    },
    data: function () {
        return {
            isLoading: false,
            emailSubject: '',
            emailPriorityValue: '',
            duedate: '',
            Message: '',
            emailTo: '',
            emailCc: '',
            emailImportance: 'normal',
            emailBcc: '',
            emailContent: '',
            isView: false,
            modal: null,
            Template: '',
            emailTemplate: [],
            contentView: false,
            getemailContent: [],
            emailValidate: {},
            emailValidateemails: {},
            emailsubmit:false,
            noRecordForTab: false,
            editor: ClassicEditor,
            Description:''
        }
    },
    created: function () { 
        var vm=this;
        this.modal = this.EmailInfo;
        if (this.modal != null) {
            this.FetchEmailViewData(this.modal.ID);
            this.isView = true;
        }
        this.GetEmailTemplate();
        this.emailValidate = {
            required: true,
            regex: /^(\s?[^\s,]+@[^\s,]+\.[^\s,]+\s?,)*(\s?[^\s,]+@[^\s,]+\.[^\s,]+)$/
        }
        this.emailValidateemails = {
            regex: /^(\s?[^\s,]+@[^\s,]+\.[^\s,]+\s?,)*(\s?[^\s,]+@[^\s,]+\.[^\s,]+)$/
        }
       setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
       
    },
    methods: {
        onReady( editor )  {
    editor.ui.getEditableElement().parentElement.insertBefore(
            editor.ui.view.toolbar.element,
            editor.ui.getEditableElement()
    );
  },
        GetEmailTemplate: function () {
            var vm = this;
            var url = "id=&moduleName=" + vm.modulename + "&subModuleName=" + vm.submodulecode+ "&type=EMAILTEMPLATE";             
           LeadDataService.GetDDLDataBySubModule(url)
                .then(function (response) {
                    var json = response.data;
                     
                    if (json != null) {
                        vm.emailTemplate = json.DATA;
                        return vm.emailTemplate;
                    }
                     
                });
        },
        loadEmailTemplate: function (value) {
            if (value != '') {
                var vm = this;
                var templateID = value
                var url = "id=" + templateID;
                 
             LeadDataService.GetEmailData(url)
                    .then(function (response) {
                        var json = response.data;
                         
                        if (json != null) {
                            vm.Description=json.data[0].template_content;
                                                                                             
                        }
                         
                    });
            }
            else {
                vm.$refs['editor'].setHTML('');
            }
        },
       
        saveEmail: function () {          
            var vm = this;         
            // vm.$refs.form.setFieldTouched('Description',true);  
            // vm.$refs.form.setFieldTouched('Template', true);  
            // vm.$refs.form.setFieldTouched('emailSubject', true);
            // vm.$refs.form.setFieldTouched('emailTo', true);
            vm.$refs.form.validate().then((result) =>{               
                if (result) {
                    vm.isLoading = true;    
                    vm.emailsubmit = true; 
                                 
                    var data = {
                        from: vm.email,
                        to: vm.emailTo,
                        emailSubject: vm.emailSubject,
                        body: vm.Description,
                        configModuleName: 'Email',
                        moduleName: vm.modulename,
                        submodulecode: vm.submodulecode,
                        emailPriorityValue: vm.emailImportance,
                        emailCc: vm.emailCc,
                        emailBcc: vm.emailBcc,
                        slecteditems: vm.id,                        
                        templatename: vm.Template,
                        mailStatus: 1
                    };
                    var objectPostString = JSON.stringify(data);
                  LeadDataService.AddEmailTemplate(objectPostString).then(function (response) {
                  
                        var emailLogcontent = "";
                         var logId =0;
                         var mainId=JSON.parse(response.data[0].Value).MAIN_ID;
                        
                         if(mainId && mainId.indexOf(',')!=-1){
                             logId= mainId.split(',')[1];
                         }else{
                            logId= mainId;
                         }
                        
                        if (response.data[0].Status == "Success") {
                            var url ="id=" + logId;
                          LeadDataService.GetEmailLog(url) .then(function (responseLog) {
                                    var json = responseLog.data;
                                    if (json != null && json!='') {
                                        vm.getemailContent = json.data;
                                        emailLogcontent = vm.getemailContent[0].email_content;
                                    }
                                    if (responseLog.data != null) {
                                        var postJSONNew = {
                                            from: vm.email,
                                            to: vm.emailTo,
                                            Subject: vm.emailSubject,
                                            body: emailLogcontent,
                                            configModuleName: 'Email',
                                            moduleName: vm.modulename,
                                            subModuleCode: vm.submodulecode,
                                            emailPriorityValue: vm.emailImportance,
                                            emailCc: vm.emailCc,
                                            emailBcc: vm.emailBcc,
                                            refId: vm.id,                                           
                                            mailStatus: 1,
                                            isSaveLog: 'no'
                                        };
                                        var objectPostStringNew = JSON.stringify(postJSONNew);                                        
                                        LeadDataService.SendEmail(objectPostStringNew).then(function (responseSend) { 
                                            vm.isLoading = false;
                                            if (responseSend.status == 200 && responseSend.data[0].Status !='Failure') {
                                             vm.ShowAlert(vm.$t('EmailSentSuccessMessage',[vm.$t(postJSONNew.to)]), "success", true,vm.$t("Alert"));
                                                vm.$emit('close');
                                                vm.$parent.loadData('email');                                               
                                            }
                                            else {
                                                vm.ShowAlert(vm.$t("Email Sent Failed on this email id. Please check your email id."),"warning", true,vm.$t("Alert"));
                                                vm.$parent.loadData('email');
                                            }
                                            vm.emailsubmit = false;
                                            vm.isLoading = false; 
                                        });
                                    }
                                });
                        }
                    });
                }
                vm.isLoading = false;
            });
        },
        FetchEmailViewData: function (id) {
            var vm = this;
            var url ="id=" + id + "&refId=" + vm.id + "&subModuleCode=" + vm.submodulecode;
           LeadDataService. EmailListing(url).then(function (response) {
                 
                if (response.data != null) {
                    var data = response.data.data;
                    vm.emailSubject = data[0].EMAIL_SUBJECT;
                    vm.emailTo = data[0].EMAIL_TO;
                    vm.emailCc = data[0].EMAIL_CC;
                    vm.emailImportance = data[0].EMAIL_PRIORITY;
                    vm.emailBcc = data[0].EMAIL_BCC;
                    vm.emailContent = data[0].EMAIL_CONTENT;
                    var frmFields={}
                     frmFields['emailSubject']=vm.emailSubject;
                     frmFields['emailTo']=vm.emailTo;
                     frmFields['emailCc']=vm.emailCc;
                     frmFields['emailImportance']=vm.emailImportance;
                     frmFields['emailBcc']=vm.emailBcc;
                     frmFields['Description']=vm.emailContent;                    
                    //  vm.$refs.form.resetForm({values:frmFields});
                    
                }
            });
        }
    }

    }
</script>

<template>
  
      
        <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
       <div class="modal-dialog">
     <div class="modal-content">
       <div class="modal-header">
         <slot name="headerForModal">
      <h5 class="modal-title">{{ $t("SendEmail")}}  </h5>
        </slot>
        <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
        </div>
          <div class="modal-body">
       <Form  ref="form"  v-slot="{ values,meta}">
        <div class="d-flex flex-wrap">
       <div class="col-md-12 p-0">
        <div class="row">
       <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
         <div class="form-group">
       <label>{{ $t("Subject")}}:<span class="text-danger">*</span></label>
       <Field name="Email Subject" :rules="{'required':true}" label="Subject" v-slot="{errors,touched,invalid}" >
                                            <input   type="text" v-bind:maxlength="50" v-model="emailSubject" name="emailSubject" v-bind:disabled="isView"  
                                            v-bind:class="{'form-control' : true,'is-invalid': touched && invalid}" />
                                            <span   name="Email Subject" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>   
       
       </div>
   </div>
        <div v-show="isView!= true" class="col-sm-col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
     <div class="form-group">
       <label> {{$t("SelectTemplate")}}:<span class="text-danger">*</span></label>
       <Field id="" name="Email Template" :rules="{ 'required': true }" label="Email Template"
               v-slot="{ errors, touched, invalid }">
                <select v-bind:class="{ 'form-control': true, 'is-invalid': touched && invalid }" v-bind:disabled="isView" v-model="Template" 
                    v-on:change="loadEmailTemplate(Template)">
                    <option value=""> {{ $t("PleaseSelect") }} {{ $t("Template") }}</option>
                 <option v-for="(email,emailIndex) in emailTemplate" v-bind:value="email.value" :key="emailIndex">{{ email.name }}</option>
                </select>
                <span   name="Email Template" class="invalid-feedback d-block">{{ errors[0] }}</span> 
        </Field>      
        </div>
       </div>
        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
       <div class="form-group">
          <label> {{$t("Importance")}}:</label>
        <div class="pl-0 border-0">
        <div class="form-check form-check-inline"><input type="radio" v-bind:disabled="isView" :class="{'disableradio':isView}" v-model.trim="emailImportance" v-bind:checked="emailImportance== 'normal'" class="form-check-input" value="normal" name="emailImportance" /> {{$t("Normal")}}  </div>
       <div class="form-check form-check-inline"><input type="radio" v-bind:disabled="isView" :class="{'disableradio':isView}" v-model.trim="emailImportance" v-bind:checked="emailImportance== 'low'" class="form-check-input" value="low" name="emailImportance" />{{ $t("Low") }} </div>
       <div class="form-check form-check-inline"><input type="radio" v-bind:disabled="isView" :class="{'disableradio':isView}" v-model.trim="emailImportance" v-bind:checked="emailImportance== 'high'" class="form-check-input" value="high" name="emailImportance" /> {{$t("High")}} </div>
       </div>
        </div>
       </div>
       <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
        <div class="form-group">
        <label> {{$t("To")}} :<span class="text-danger">*</span></label>
        <Field name="Email To" :rules="{'required':true,'email':true}" label="Email To" v-slot="{errors,touched,invalid}" >
                                            <input    type="text" v-bind:maxlength="50" v-model.trim="emailTo" name="emailTo" v-bind:disabled="isView" 
                                            v-bind:class="{'form-control' : true,'is-invalid': touched && invalid}" />
                                            <span   name="Email To" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>  </div>
   </div>
       <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
        <div class="form-group">
        <label> {{$t("Cc")}} :</label>
     
       <Field name="emailCc" :rules="{'required':false,'email':true}" label="Email Cc" v-slot="{errors,touched,invalid}" >
                                            <input    type="text" v-bind:maxlength="50" v-model="emailCc" name="emailCc" v-bind:disabled="isView"
                                            v-bind:class="{'form-control' : true,'is-invalid': touched && invalid}" />
                                            <span   name="emailCc" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>   
       </div>
       </div>
        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
       <div class="form-group">
        <label>{{ $t("Bcc") }}:</label>
       
        <Field name="emailBcc" :rules="{'required':false,'email':true}" label="Email Bcc" v-slot="{errors,touched,invalid}" >
                                            <input   type="text" v-bind:maxlength="50" v-model="emailBcc" name="emailBcc" v-bind:disabled="isView"
                                            v-bind:class="{'form-control' : true,'is-invalid': touched && invalid}" />
                                            <span   name="emailBcc" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>   
       </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
       <div class="form-group">
          <label>{{ $t("Description")}}:<span class="text-danger">*</span></label>
          <Field name="Description" :rules="{'required':true}" label="Description" v-slot="{errors,touched,invalid}"> 
            <ckeditor :editor="editor" v-model="Description"></ckeditor>       
        
  <span   name="Description" class="invalid-feedback d-block">{{errors[0]}}</span>
  </Field>
  
     <small v-if="contentView" class="text-danger"> {{$t("DescRequired")}} </small>
        </div>
        </div>
        </div>
        </div>
       </div>
        <div class="row flex-row-reverse" v-if="!isView">
            <div class="col-lg-6 text-right">
       <a href="javascript:;" :title="$t('Send')" class="btn btn-success mx-1" v-bind:class="{'disabled': emailsubmit}" v-on:click="saveEmail(values,meta)"><em class="fa fa-send pr-2"></em>{{ $t("Send") }} </a>

        <a href="javascript:;" :title="$t('Cancel')"  v-on:click="$emit('close')" class="btn btn-danger mx-1"><em class="fa fa-times"></em> {{$t("Cancel") }} </a>
            </div>
            <div class="col-lg-6">
                <div class="py-2">
                   <small class="text-danger">{{ $t("MandatoryString")}} </small>
                </div>
            </div>
       </div>
       </Form>
        </div>
        </div>
       </div>
       </div>
      
        
</template>
<style>
.disableradio{
opacity: 0 !important;
}
</style>