            shippingCountyData: '',
            shippingCityData: '',
            billingZipCodeData: '',
            shippingZipCodeData: '',
            billingEmailData: '',
            shippingEmailData: '',
            billingUserPhoneData: '',
            shippingUserPhoneData: '',
            address: '',
            phoneNo: '',
            orderTitle: '',
            description: '',
            termAndCondition: '',
            subtotal: '',
            shippingCharges: '',
            adjustments: '',
            netTotal: '',
            separatingChars: {
                String: "_"
            },
            CRMInvoice: false,
            modalItem: null,
            quotationNumber: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            isView: false,
            modal: null,
            containername:null,
            stausName: false,
            currentTab: "",
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize: '',
            disallowedfiletypes: '',
            userid: null,
            companyId: null,
            email:null,
            isShowNotePopup: false,
            editUrl: `/CRM/ManageInvoice/${this.$route.params.id}/CRM/CRM_INVOICE`,
            PreviewData:null,
            companyLogo:'',
            isLoading:false
        }
    },
    created: function () {
        this.userid= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.email= this.GetUserInfo.Email;
        this.containername=this.GetUserInfo.ContainerName;
        var VueObj = this;
        VueObj.GetCompanyLogo();
        VueObj.GetFileSettings('CRMPerFileSize');
        VueObj.GetFileSettings('CRMDisallowedFileTypes');
        VueObj.FetchData();
    },
    updated:function()
     {
        var vm = this; 
        setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
     $("#ancActivityExpandAllCollapseAll").click(function () {
        if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
          }); 
     },
    methods: {
        DisableButton: function () {
            var VueObj = this;
            VueObj.isActivities = false;
            VueObj.isNotes = false;
            VueObj.isAttachment = false;
            VueObj.isSendEmail = false;
        },
        openEmailModal: function (item) {
            this.sendEmailModal = true;
            this.modalItem = item;
        },
        DeleteActivity(item) {
           
            this.modalDeleteItem = item;
            this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
        },
        DeleteActivityData: function (id) {
            var vm = this;
            if (id > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
                  
                    var postJSON = {
                        ids: id,
                        moduleName: "CRM",
                        subModuleCode: "CRM_INVOICE",
                        refCode: "DELETE_VIEW_ACTIVITY",
                        refId: vm.sourceId
                    };
                    var objectPostString = JSON.stringify(postJSON);
                  
                    InvoiceDataService.CommonDelete(objectPostString).then(
                        function (response) { 
                            if (response.data[0].Status == "Success") {
                                vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("Activity")]), "success", true, vm.$t("Alert"));
                                vm.loadData("activities");
                            } else {                               
                                vm.ShowAlert(vm.$t("DeletedError", [vm.$t("Activity")]), "failure", true, vm.$("Alert"));
                            }

                        }
                    );

                });
            }
        },
        GetEmailInfo: function (item) {
            this.EmailInfo = item;
            this.sendEmailModal = (this.sendEmailModal) ? false : true;
        },
        openAddTaskModel: function () {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openAddNewEvent: function () {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        openCRMAttachmentModal: function () {
            this.showAttachmentModal = (this.showAttachmentModal) ? false : true;
        },
        openCallEvent: function () {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openActivityModal: function (item) {
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        openShowNotePopup: function () {
            var ths = this;
            ths.isShowNotePopup = true;
            $("#tab-notes").trigger("click");
            ths.loadData("notes");
        },
        FetchData: function () {
            var VueObj = this;
            VueObj.isLoading=true;
            VueObj.timelinedata = [];
            InvoiceDataService.FormLayoutView(VueObj.ApiURL).then(function (response) {
                VueObj.isLoading=false;
                var fullLeftJSON = response.data.LEFTDATA;
                var stausName = $.grep(fullLeftJSON, function (element, index) {
                    return element.name == 'status_id';
                });
                if (stausName != null && stausName.length > 0) {
                    VueObj.stausName = stausName[0];
                }
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    return element.form_field_visibility == 'YES';
                });
                if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                    var fullJSON = response.data.RIGHTDATA;
                    var gruopBy = "";
                    $.each(fullJSON, $.proxy(function (index, item) {
                        gruopBy =VueObj.$options.filters.formatDate(item.Complete_datetime);
                        var chkGroup = $.grep(VueObj.timelinedata, function (element, index) {
                            return element.groupKey == gruopBy;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullJSON, function (element, index) {
                                return VueObj.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    groupKey: gruopBy,
                                    groupData: returnedData
                                };
                                VueObj.timelinedata.push(objGroup);
                            }
                        }
                    }, VueObj));
             
                    VueObj.noRecordForTab = false;
                } else {
                    VueObj.noRecordForTab = true;
                }
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;
                    $.each(fullLeftJSON, $.proxy(function (index, item) {
                        if (item.name == 'order_number') {
                            VueObj.quotationNumber = item.value;
                        }
                        var chkGroup = $.grep(VueObj.LeftViewData, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    LeftViewData: returnedData
                                };
                                VueObj.LeftViewData.push(objGroup);
                            }
                        }
                        groupId = item.group_id;
                    }, VueObj));
                  
                }
            });
        },
        loadData: function (tabName,callFrom) {
            var vm = this;  
            vm.DisableButton(); 
            if (tabName == "notes") {
                vm.isNotes = true;
                vm.activeKey = 2;
                if (callFrom && callFrom == 'savenote')
                    vm.isShowNotePopup = false;
            }
            else if (tabName == "timeline") {
                vm.istimeline = true;
                vm.activeKey = 1;
            }
            else if (tabName == "attachments") {
                vm.isAttachment = true;
                vm.activeKey = 3;
            }
            else if (tabName == "activities") {
                vm.isActivities = true;
                vm.activeKey = 4;
            }
            else if (tabName == "products") {
                vm.activeKey = 5;
                vm.isProduct = true;
            }
            else if (tabName == "emailLog") {
                vm.activeKey = 6;
                vm.isSendEmail = true;
            }
            else if (tabName == "calls") {
                vm.activeKey = 7;
                vm.iscalls = true;
            }
            else if (tabName == "feedback") {
                vm.activeKey = 8;
                vm.isFeedback = true;
            }
            else if (tabName == "email") {
                vm.Isdownloadpdf=true;
                url =`id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&type=INVOICE_TEMPLATE`;
                InvoiceDataService.getDDLData(url).then(function (response) {
                              var json = response.data;
                               
                              if (json != null) {
                                  vm.TemplateName = json.DATA[0].name;
                                  if (vm.TemplateName == "Bill1") {
                                      vm.template1 = true;
                                      vm.template2 = false;
                                      vm.template3 = false;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill2") {
                                      vm.template1 = false;
                                      vm.template2 = true;
                                      vm.template3 = false;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill3") {
                                      vm.template1 = false;
                                      vm.template2 = false;
                                      vm.template3 = true;
                                      vm.template4 = false;
                                  }
                                  else if (vm.TemplateName == "Bill4") {
                                      vm.template1 = false;
                                      vm.template2 = false;
                                      vm.template3 = false;
                                      vm.template4 = true;
                                  }
                                   
                                  if (vm.TemplateName != null) {
                                    var url =`id=${vm.$route.params.id}&moduleCode=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}`;
                                    InvoiceDataService.QuotationDetails(url).then(function (response) {
                                            vm.PreviewData = response.data;
                                            if (response.data != null) {
                                                var productSummaryData = [];
                                                var i = 1;
                                                $.each(response.data.dataproductlineitemjson, function (key, item) {
                                                    var prductName = "";
                                                    var prductId = 0;
                                                    if (item.PRODUCT_NAME != null) {
                                                        try {
                                                            item.PRODUCT_NAME = JSON.parse(item.PRODUCT_NAME);
                                                        }
                                                        catch (ex) { }
                                                        if (Array.isArray(item.PRODUCT_NAME)) {
                                                            prductName = item.PRODUCT_NAME[0].name;
                                                            prductId = item.PRODUCT_NAME[0];
                                                        }
                                                        else {
                                                            prductName = item.PRODUCT_NAME;
                                                        }
                                                    } else {
                                                        prductName = item.ITEM_NAME;
                                                    }
                                                    var dataObject = {
                                                        SNo: i,
                                                        ProductId: prductId,
                                                        ProductName: prductName,
                                                        ProductList: item.ITEM_PRODUCT_LIST_PRICE,
                                                        ProductQuantity: item.ITEM_PRODUCT_QUANTITY,
                                                        ProductAmount: item.ITEM_PRODUCT_AMOUNT,
                                                        ProductDiscount: item.ITEM_PRODUCT_DISCOUNT,
                                                        ProductDesc: item.ITEM_PRODUCT_DESC,
                                                        ProductTax: JSON.parse(item.TAX_DATA) == null ? "0" : JSON.parse(item.TAX_DATA)[0].TAX_RATE,
                                                        ProductTotal: item.ITEM_TOTAL,
                                                    };
                                                    productSummaryData.push(dataObject);
                                                    i++;
                                                });
                                                if (productSummaryData.length > 0) {
                                                    vm.SummaryData = productSummaryData;
                                                }
                                                else {
                                                    vm.SummaryData = "";
                                                }
                                                vm.CRMInvoice = true;
                                                vm.noRecordForTab = false;
                                            }
                                            else {
                                                vm.CRMInvoice = false;
                                                vm.noRecordForTab = true;
                                            }
                                             
                                        });
                                        vm.activeKey = 9;
                                  }
                              }
                              else {
                                   
                                  this.noRecordForTab = true;
                              }
                          });
                vm.activeKey = 9;
                vm.isEmail = true;
            }
            vm.currentTab = tabName;
            if (tabName != "" && tabName != null) {
                if (tabName == "attachments") {
                    InvoiceDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.attachmentData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "notes") {

                    vm.noteData = [];
                    InvoiceDataService.NoteListing(vm.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            vm.noteData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.noteData = [];
                        }
                    });
                }
                else if (tabName == "activities") {
                    InvoiceDataService.ActivityListing(vm.ApiURL).then(function (response) {
                        if (response.data != "" && response.data != null && response.data.data.length > 0) {
                            vm.activitiesData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "products") {
                    var url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                    InvoiceDataService.getItemListForAssociate(url).then(function (response) {
                        if (response.data != null) {
                            vm.productData = response.data.data;
                            vm.productHeaders = response.data.schema;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.productData = null;
                        }
                    });
                }
                else if (tabName == "calls") {
                    InvoiceDataService.GetCallLogsListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.callData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.calltData = null;
                        }
                    });
                }
                else if (tabName == "timeline") {
                    InvoiceDataService.TimelineListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(fullJSON, $.proxy(function (index, item) {
                                gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                var chkGroup = $.grep(this.timelinedata, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(fullJSON, function (element, index) {
                                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData
                                        };
                                        vm.timelinedata.push(objGroup);
                                    }
                                }
                            }, vm));
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "emailLog") {
                    var url= `id=0&refId=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                                        InvoiceDataService.EmailListing(url).then(function (response) {
                        if (response.data != "") {
                            vm.EmailLogData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                    vm.isSendEmail = true;
                }
                else if (tabName == "feedback") {
                    var url = `id=${this.$route.params.id}`;
                    InvoiceDataService.GetFeedBack(url).then(response => {
                        if (response.data != "" && response.data != null && response.data.data.length > 0) {
                            vm.feedbackData = response.data.data;
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
            }
        },
        GetFileSettings:function(configkey)
        {
            var vm = this;
            InvoiceDataService.GetConfigurationSettings("configkey="+ configkey).then(
                function (response) {  
                    if (response.data !="") {
                        if(configkey=='CRMPerFileSize')
                        {
                            vm.maxsize=response.data;
                        }
                        else
                        {
                            vm.disallowedfiletypes=response.data;
                        }
                    } else {                               
                        return '';
                    }

                }
             );
        },
        GetCompanyLogo(){
            var vm = this;
            InvoiceDataService.GetCompanyLogo().then(function(response){
                vm.companyLogo = response.data;
            });
        },
        DownlaodPDF:function(){
            var vm=this;
            var postJSON = {
                    moduleName: vm.$route.params.moduleName,
                    subModuleCode: vm.$route.params.subModuleCode,
                    refId: vm.$route.params.id,
                    quoteCompanyLogo: 'quotationLogo',
                    companyLogo:vm.companyLogo
                };
                vm.isLoading=true;
                var objectPostString = JSON.stringify(postJSON);
            InvoiceDataService.DownlaodPDF(objectPostString).then(function(response){
                var result = response.data;
                const linkSource = `data:${result.fileType};base64,${result.bytedata}`;
                const downloadLink = document.createElement("a");
                downloadLink.href = linkSource;
                downloadLink.download = result.fileName;
                downloadLink.click();
                vm.isLoading=false;
            }); 
        }
    }
}
</script>
