<template>
  <div>
    <div class v-if="!noRecordForTab">
      <div
        class="table-responsive tableborder customborder_lightgrey float-left crm_rightpanelmaximumscroll_table_btns"
      >
        <table class="table table-bordered mb-0" id="table-call-content" aria-describedby="crmcall">
          <thead class="thead-bg">
            <tr>
               <th scope="col">
                <span>{{$t('Call')}} {{$t('Type')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('Call')}} {{$t('Form')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('Call')}}{{$t('To')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('EventStartTime')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('EventEndTime')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('Duration')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('Comment')}}</span>
              </th>
               <th scope="col">
                <span>{{$t('Status')}}</span>
              </th>'
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in calldata1" :key="item">
              <td>{{item.CALL_TYPE}}</td>
              <td>{{item.CALL_TO}}</td>
              <td>{{formatDateTime(item.START_TIME)}}</td>
              <td>{{formatDateTime(item.END_TIME)}}</td>
              <td>{{item.DURATION}}</td>
              <td>{{item.COMMENT}}</td>
              <td class="text-center">
                <span
                  v-bind:class="{'px-2 bg-success text-white d-block': (item.STATUS_ID == '1001')}"
                >{{$t('Active')}}</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-if="noRecordForTab" class="text-center text-danger p-4 border">{{$t('NoRecordfound')}}</div>
  </div>
</template>

<script>
export default {
        props: {
        calldata: null,
        sourceid: {
            type: String,
            required: true
        }
      
        
    },
    data: function () {
        return {
            calldata1: [],
            noRecordForTab: true
        }
    },
    watch: {
        calldata: function (value) {
            var vm =this;
            vm.calldata1 = value;
            vm.noRecordForTab = ((vm.calldata1 != null && vm.calldata1.length > 0) ? false : true);
            setTimeout(function(){vm.ResponsiveDataTable('table-call-content')}, 500);
        }
    },    
    methods: {        
        formatDate: function(value) {
            if (value) {
                return moment(String(value), "YYYY-MM-DD").format(String("MM-dd-yyyy"))
            }
        },
        formatDateTime: function(value) {
            if (value) {
                try {
                    var timeFormat = 'h:mm A';
                    timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                    var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());

                    return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String("MM-dd-yyyy" + " " + String(timeFormat)));

                } catch (e) {
                    var timeFormat = 'h:mm A';
                    timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                    return moment(String(value)).format(String("MM-dd-yyyy" + " " + String(timeFormat)));
                }
            }
        },
    },
}
</script>