<script>
import LeadDataService from "@/services/DataService";
    export default{
        props:{
            Data:{
                type:Object,
                required:true
            },
            SummaryData:{
                type:Object,
                required:true
            },
            TemplateName:{
                type:String,
                required:true
            },
            Header:{
                type:String,
                required:true
            }
        },
        data:function(){
            return{
                addressData:null,
                currency:null,
                symbol:null,
                companyLogo:null,
                orderNumber: '',
                createdAt: '',
                companyName: '',
                clientName: '',
                billingContactPerson: '',
                shippingContactPerson: '',
                billingFullAddress: '',
                billingAccountAddress: '',
                shippingFullAddress: '',
                shippingAccountAddress: '',
                billingCountryData: '',
                billingCityData:'',
                shippingCountryData: '',
                shippingCityData:'',
                billingStateData: '',
                shippingStateData: '',
                billingCountyData: '',
                shippingCountyData: '',
                billingZipCodeData: '',
                shippingZipCodeData: '',
                billingEmailData: '',
                shippingEmailData: '',
                billingUserPhoneData: '',
                shippingUserPhoneData: '',
                address: '',
                email: '',
                phoneNo: '',
                orderTitle: '',
                description: '',
                termAndCondition: '',
                subtotal: '',
                shippingCharges: '',
                adjustments: '',
                netTotal: '',
            }
        },
        created:function(){
            debugger;
            var vm = this;
            vm.GetCompanyLogo();
            vm.currency=vm.GetUserInfo.Currency;
            vm.symbol= vm.currency=="(USD)" ? "$" :vm.currency;
            vm.addressData = vm.Data.contact_person;
            if (vm.addressData[0] !== undefined && vm.addressData[0] != null) {
                vm.orderNumber = vm.addressData[0].ORDER_NUMBER == null ? '' : vm.addressData[0].ORDER_NUMBER;
                vm.createdAt = vm.addressData[0].CREATED_AT == null ? '' : vm.addressData[0].CREATED_AT;
                vm.companyName = vm.addressData[0].COMPANY_NAME == null ? '' : vm.addressData[0].COMPANY_NAME;
                vm.address = vm.addressData[0].ADDRESS == null ? '' : vm.addressData[0].ADDRESS;
                vm.email = vm.addressData[0].EMAIL == null ? '' : vm.addressData[0].EMAIL;
                vm.phoneNo = vm.addressData[0].PHONE_NUMBER == null ? '' : vm.addressData[0].PHONE_NUMBER;
                vm.clientName = vm.addressData[0].ACCOUNT_NAME == null ? '' : vm.addressData[0].ACCOUNT_NAME;
                vm.billingContactPerson = vm.addressData[0].CONTACT_PERSON_NAME == null ? '' : vm.addressData[0].CONTACT_PERSON_NAME;
                vm.billingAccountAddress = (vm.addressData[0].ACCOUNT_ADDRESS == null || vm.addressData[0].ACCOUNT_ADDRESS == '') ? '' : vm.addressData[0].ACCOUNT_ADDRESS + ',';
                vm.billingCountryData = (vm.addressData[0].COUNTRY_NAME == null || vm.addressData[0].COUNTRY_NAME == '') ? '' : vm.addressData[0].COUNTRY_NAME + ',';
                vm.billingCityData = (vm.addressData[0].CITY_NAME == null || vm.addressData[0].CITY_NAME == '') ? '' : vm.addressData[0].CITY_NAME + ',';
                vm.billingStateData = (vm.addressData[0].STATE_NAME == null || vm.addressData[0].STATE_NAME == '') ? '' : vm.addressData[0].STATE_NAME + ',';
                vm.billingCountyData = (vm.addressData[0].COUNTY_NAME == null || vm.addressData[0].COUNTY_NAME == '') ? '' : vm.addressData[0].COUNTY_NAME + ',';
                vm.billingZipCodeData = (vm.addressData[0].ZIP_CODE_NAME == null || vm.addressData[0].ZIP_CODE_NAME == '') ? '' : vm.addressData[0].ZIP_CODE_NAME + ',';
                vm.billingEmailData = vm.addressData[0].EMAIL_ID == null ? '' : vm.addressData[0].EMAIL_ID;
                vm.billingUserPhoneData = vm.addressData[0].MOBILE_PHONE == null ? '' : vm.addressData[0].MOBILE_PHONE;
                vm.orderTitle = vm.addressData[0].ORDER_TITLE == null ? '' : vm.addressData[0].ORDER_TITLE;
                vm.description = vm.addressData[0].DESCRIPTION == null ? '' : vm.addressData[0].DESCRIPTION;
                vm.termAndCondition = vm.addressData[0].TERM_AND_CONDITIONS == null ? '' : vm.addressData[0].TERM_AND_CONDITIONS;
                vm.billingFullAddress = vm.billingAccountAddress + vm.billingCountryData + vm.billingCityData + vm.billingZipCodeData;
                vm.billingFullAddress = vm.removeLastChar(vm.billingFullAddress, ',');
            }
            if (vm.addressData[1] !== undefined && vm.addressData[1] != null) {
                vm.shippingContactPerson = vm.addressData[1].CONTACT_PERSON_NAME == null ? '' : vm.addressData[1].CONTACT_PERSON_NAME;
                vm.clientName = vm.addressData[1].ACCOUNT_NAME == null ? '' : vm.addressData[1].ACCOUNT_NAME;
                vm.shippingAccountAddress = (vm.addressData[1].ACCOUNT_ADDRESS == null || vm.addressData[1].ACCOUNT_ADDRESS == '') ? '' : vm.addressData[1].ACCOUNT_ADDRESS + ',';
                vm.shippingCountryData = (vm.addressData[1].COUNTRY_NAME == null || vm.addressData[1].COUNTRY_NAME == '') ? '' : vm.addressData[1].COUNTRY_NAME + ',';
                vm.shippingCityData = (vm.addressData[1].CITY_NAME == null || vm.addressData[1].CITY_NAME == '') ? '' : vm.addressData[1].CITY_NAME + ',';
                vm.shippingStateData = (vm.addressData[1].STATE_NAME == null || vm.addressData[1].STATE_NAME == '') ? '' : vm.addressData[1].STATE_NAME + ',';
                vm.shippingCountyData = (vm.addressData[1].COUNTY_NAME == null || vm.addressData[1].COUNTY_NAME == '') ? '' : vm.addressData[1].COUNTY_NAME + ',';
                vm.shippingZipCodeData = (vm.addressData[1].ZIP_CODE_NAME == null || vm.addressData[1].ZIP_CODE_NAME == '') ? '' : vm.addressData[1].ZIP_CODE_NAME + ',';
                vm.shippingEmailData = vm.addressData[1].EMAIL_ID == null ? '' : vm.addressData[1].EMAIL_ID;
                vm.shippingUserPhoneData = vm.addressData[1].MOBILE_PHONE == null ? '' : vm.addressData[1].MOBILE_PHONE;
                vm.shippingFullAddress = vm.shippingAccountAddress + vm.shippingCountryData + vm.shippingCityData + vm.shippingZipCodeData;
                vm.shippingFullAddress = vm.removeLastChar(vm.shippingFullAddress, ',');
            }
            var amount = vm.Data.dataproductlineitemjsonamount;
            var jsonAmount = vm.Data.dataproductlineitemjsonamount[0];
            if (amount !== undefined && amount != null && jsonAmount !== undefined && jsonAmount != null) {
                vm.subtotal = jsonAmount.summary_sub_total == null ? '' : jsonAmount.summary_sub_total;
                vm.shippingCharges = jsonAmount.summary_shipping_charges == null ? '' : jsonAmount.summary_shipping_charges;
                vm.adjustments = jsonAmount.summary_adjustments == null ? '' : jsonAmount.summary_adjustments;
                vm.netTotal = jsonAmount.summary_total == null ? '' : jsonAmount.summary_total;
            }
        },
        methods:{
            GetCompanyLogo(){
                var vm = this;
                LeadDataService.GetCompanyLogo().then(function(response){
                    vm.companyLogo = response.data;
                });
            }
        }
    }
</script>

