<template>
    <div class="listing">

        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0" v-if="!NoRecordForTab">
                                    <div class="table-responsive tableborder customborder_lightgrey float-left crm_rightpanelmaximumscroll_table_btns">
                                        <table class="table table-bordered mb-0" id="table-email-content" aria-describedby="crmsendemail">
                                            <thead class="thead-bg">
                                               <tr>
                                                     <th scope="col"   ><span>{{$t('Subject')}}</span></th>
                                                     <th scope="col"   ><span>{{$t('Date')}}</span></th>
                                                    <th scope="col"   ><span>{{$t('Send')}}{{$t('To')}}</span></th>
                                                   <th scope="col"   ><span>{{$t('Send')}}{{$t('By')}}</span></th>
                                                    <th scope="col"    class="text-center" style="max-width:70px;"><span>{{$t('Status')}}</span></th>
                                               </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item,index) in EmailLogData" :key="index">
                                                    <td><a href="javascript:;" v-on:click="openEmailModal(item);">{{item.EMAIL_SUBJECT}}</a></td>
                                                    <td>{{item.EMAIL_DATE | formatDate}}</td>
                                                    <td>{{item.EMAIL_TO}}</td>
                                                    <td>{{item.EMAIL_SENT_BY}}</td>
                                                    <td class="text-center"><span v-bind:class="{'': true,'bg-danger text-white d-block' : item.MAIL_STATUS =='Pending','bg-success text-white d-block' : item.MAIL_STATUS =='Sent'}">{{item.MAIL_STATUS}}</span></td>
                                                </tr>
                                            </tbody>
                                       </table>
                                  </div>
                                </div>
                                <div v-if="NoRecordForTab" class="text-center text-danger p-4 border"> {{$t('NoRecordfound')}}</div>

                         
    </div>
</template>
<script>
export default {
     props: {
        emaildata: null,
          },
    data: function () {
        return {
            EmailLogData: [],
            NoRecordForTab: true,
            sendEmailModal: false,
            modalItem: null,
        }
    },
    watch: {
        emaildata: function (value) {
            let vm=this;
            vm.EmailLogData = value;
            vm.NoRecordForTab = (vm.EmailLogData != null && vm.EmailLogData.length > 0) ? false : true;
            setTimeout(function(){vm.ResponsiveDataTable('table-email-content')}, 500);
        }
    },
    methods: {
        openEmailModal: function (item) {
            var type = 'AddEmail';
            if (item != null) {
                type = 'UpdateEmail';
            }
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission(type)) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
                this.$emit('GetEmailInfo',item);
            }
        },
    },
}
</script>