<template>
    <div>  
                <div class="" v-if="!noRecordForTab">
                        <div class="listing">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered mb-0" id="table-products-content" aria-describedby="crmproduct">
                                    <thead class="thead-bg">
                                        <tr>
                                             <th scope="col"   ><span>{{$t('ItemName')}}</span></th>
                                             <th scope="col"   ><span>{{$t('ItemCode')}}</span></th>
                                            <th scope="col"    v-if="(submodulecode=='CRM_SALES_ORDERS' || submodulecode=='CRM_PURCHASE_ORDERS')"><span> {{$t("Quantity")}} </span></th>
                                             <th scope="col"   ><span>{{$t('ItemType')}}</span></th>
                                             <th scope="col"    v-if="submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS'"><span>{{$t('Provider')}}</span></th>
                                           
                                             <th scope="col"    v-if="submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS'"><span>{{$t('Price')}}</span></th>
                                       
                                             <th scope="col"   ><span> {{$t('AssociatedAt')}}</span></th>
                                           
                                             <th scope="col"    v-if="recordstatus != 'Closed' && (submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS')" class="text-center"><span> {{$t('Action')}}</span></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item,index) in productData" :key="index">
                                            <td>{{item.name}}</td>
                                            <td>{{item.item_code}}</td>
                                          
                                            <td v-if="(submodulecode=='CRM_SALES_ORDERS' || submodulecode=='CRM_PURCHASE_ORDERS')">{{item.quantity}}</td>
                                            <td>{{item.asset_type}}</td>
                                            <td v-if="submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS'">{{item.ProviderName}}</td>
      
                                            <td v-if="submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS'">{{item.unit_price}}</td>
                                           
                                            <td>{{item.created_at | formatDate}}</td>
                                     
                                            <td v-if="recordstatus != 'Closed' && (submodulecode=='CRM_LEADS' || submodulecode=='CRM_DEALS')" class="text-center single-action mobileactions"><a href="javascript:;" v-on:click="deassociateProduct(item.asset_catalogue_id,item.AccountID)" :title="$t('DeassociateProduct')"><em class="fa fa-times action-icon text-danger"></em></a></td>
                                        </tr>
                                    </tbody>
                                </table>
        </div>
        </div>

                        </div>
                        <div v-if="noRecordForTab" class="text-center text-danger p-4 border">{{$t('NoRecordfound')}}</div>
                 
               </div>


</template>
<script>
import LeadDataService from "../../../services/DataService";
import $ from "jquery";
export default {
     props: {
        productdata: null,
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
     
        modulename: {
            type: String,
            required: true
        },
        recordstatus: {
            type: String,
            required: false
        }        
    },
    data: function () {
        return {
            productData: [],
            noRecordForTab: true,
            LeadStatus: '',
            ContactsHeader: [],
            Count:0
        }
    },
    watch: {
        productdata: function (value) {
            var vm=this;
            
            $.each(value, function (index, item) {
                item.TotalAmount = item.quantity * item.unit_price;
            });
             
            vm.productData =value?JSON.parse(JSON.stringify(value)):null;            
            vm.noRecordForTab = ((vm.productData != null && vm.productData.length > 0) ? false : true);
            setTimeout(function(){vm.ResponsiveDataTable('table-products-content')}, 500);
        }
    },    
    methods: {
        SaveQty: function (item) {
            var vm = this;
            var object = {
                account_id: item.AccountID,
                product_id: item.asset_catalogue_id,
                price: item.unit_price == "" ? 0 : item.unit_price,
                sourceid: vm.sourceid,
                quantity: item.quantity,
                submodulecode: vm.submodulecode
            }
            var json = JSON.stringify(object);
            if (vm.Count > 0) {
                vm.Count = 0;
                LeadDataService.SaveProductPrice(json).then(function (response) {
                    if (response.body.msg == "UPDATE") {
                        vm.ShowAlert(vm.$t("Quantity updated Successfully"),"success", true, vm.$t("Alert"));
                    }
                })
            }
        },
        SetTotalAmount: function (item) {
            if ($.trim(item.quantity) == "")
                item.quantity = 0;     
            item.TotalAmount = parseInt(item.quantity) * item.unit_price;
            this.Count++;
        },
    

        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
               var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
       
        formatDate: function(value) {
            if (value) {
                return moment(String(value),"YYYY-MM-DD").format(String("MM-dd-yyyy"))
            }
        },
        deassociateProduct: function(value,AccId) {
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission("DeleteProducts")) {
                this.confirmR(this.$t("additionalOwnerAuthorityWarning"), true,this.$t('Alert'));
            } else {
           
                var currentObj = this;                
                if (value > 0) {
                    if (currentObj.Count == 0) {
                        currentObj.Count++;
                        setTimeout(function () { currentObj.Count = 0; }, 300);
                        currentObj.confirmR(currentObj.$t("ConfirmDeAssociateProduct"),true,null, function (result) {
                           
                                var postJSON = {
                                    ids: value,
                                    moduleName: currentObj.modulename,
                                    subModuleCode: currentObj.submodulecode,
                                    refCode: 'DELETE_VIEW_PRODUCTS',
                                    refId: currentObj.sourceid,
                                    AccountId: AccId
                                };
                                var objectPostString = JSON.stringify(postJSON);
                             LeadDataService.CommonDelete(objectPostString).then(function (response) {                                 
                                   if (response.data[0].Status == "Success") {
                                       
                                        currentObj.ShowAlert(currentObj.$t("Product has been successfully De-associated."),"success", true, currentObj.$t("Alert"));
                                        currentObj.$parent.loadData('products');
                                    }
                                    else {
                                        currentObj.ShowAlert(currentObj.$t('ErrorDeassociateFailure',[currentObj.$t('Product')]), "success", true, currentObj.$t("Alert"));
                                    }
                                     
                                });
                            
                        });
                    }
                }
                else {
                    currentObj.ShowAlert(currentObj.$t("DeletedError", [currentObj.$t("Product")]),'failure',true,currentObj.$t('Alert'));
                }
            }
        }
    },
}
</script>